import { Component } from './Component';




// ÁªÑ‰ª∂ÂÆû‰æãÁªìÊûÑ
function createComponentInstance() {
    return {
        hooks: [], // Èí©Â≠êÊï∞ÁªÑ
        effects: [], // ÂâØ‰ΩúÁî®Êï∞ÁªÑ
        element: null, // DOM ÂÖÉÁ¥†
        component: null, // ÁªÑ‰ª∂ÂáΩÊï∞
        props: null, // ÁªÑ‰ª∂Â±ûÊÄß
    }
}
export function createElement(type, props, ...children) {
    return {
        type,
        props: {
            ...props,
            children: children.map(child =>
                typeof child === 'object' ? child : createTextElement(child))
        }
    };
}

function createTextElement(text) {
    return {
        type: "TEXT_ELEMENT",
        props: {
            nodeValue: text,
            children: []
        }
    };
}


// useState ÂÆûÁé∞
function useState(initialState) {
    // ‰ªé ReactDOM Ëé∑ÂèñÂΩìÂâçÁªÑ‰ª∂‰∏ä‰∏ãÊñá
    const currentComponent = window.getCurrentComponent()
    if (!currentComponent) {
        throw new Error("useState must be called inside a component")
    }

    const instance = currentComponent
    const index = window.getHookIndex()
    window.setHookIndex(index + 1)

    // ÂàùÂßãÂåñÈí©Â≠ê
    if (instance.hooks[index] === undefined) {
        instance.hooks[index] = {
            state: typeof initialState === "function" ? initialState() : initialState,
            queue: [],
        }
    }

    const hook = instance.hooks[index]

    // Â§ÑÁêÜÁä∂ÊÄÅÊõ¥Êñ∞ÈòüÂàó
    hook.queue.forEach((action) => {
        hook.state = typeof action === "function" ? action(hook.state) : action
    })
    hook.queue = []

    const setState = (action) => {
        hook.queue.push(action)
        // Ëß¶ÂèëÈáçÊñ∞Ê∏≤Êüì
        window.scheduleRerender(instance)
    }

    return [hook.state, setState]
}

// useReducer ÂÆûÁé∞
function useReducer(reducer, initialArg, init) {
    const currentComponent = window.getCurrentComponent()
    if (!currentComponent) {
        throw new Error("useReducer must be called inside a component")
    }

    const instance = currentComponent
    const index = window.getHookIndex()
    window.setHookIndex(index + 1)

    // ÂàùÂßãÂåñÈí©Â≠ê
    if (instance.hooks[index] === undefined) {
        const initialState = init ? init(initialArg) : initialArg
        instance.hooks[index] = {
            state: initialState,
            queue: [],
        }
    }

    const hook = instance.hooks[index]

    // Â§ÑÁêÜÁä∂ÊÄÅÊõ¥Êñ∞ÈòüÂàó
    hook.queue.forEach((action) => {
        hook.state = reducer(hook.state, action)
    })
    hook.queue = []

    const dispatch = (action) => {
        hook.queue.push(action)
        // Ëß¶ÂèëÈáçÊñ∞Ê∏≤Êüì
        window.scheduleRerender(instance)
    }

    return [hook.state, dispatch]
}

// useEffect ÂÆûÁé∞
function useEffect(callback, dependencies) {
    const currentComponent = window.getCurrentComponent()
    if (!currentComponent) {
        throw new Error("useEffect must be called inside a component")
    }

    const instance = currentComponent
    const index = window.getHookIndex()
    window.setHookIndex(index + 1)

    // ÂàùÂßãÂåñÈí©Â≠ê
    if (instance.hooks[index] === undefined) {
        instance.hooks[index] = {
            callback,
            dependencies,
            cleanup: null,
            hasRun: false,
        }
    }

    const hook = instance.hooks[index]
    const prevDependencies = hook.dependencies

    // Ê£ÄÊü•‰æùËµñÈ°πÊòØÂê¶ÊîπÂèò
    const hasChanged =
        !hook.hasRun ||
        !dependencies ||
        !prevDependencies ||
        dependencies.length !== prevDependencies.length ||
        dependencies.some((dep, i) => !Object.is(dep, prevDependencies[i]))

    if (hasChanged) {
        // Ê∏ÖÁêÜ‰∏ä‰∏Ä‰∏™ÂâØ‰ΩúÁî®
        if (hook.cleanup && typeof hook.cleanup === "function") {
            hook.cleanup()
            hook.cleanup = null
        }

        // Êõ¥Êñ∞Èí©Â≠ê‰ø°ÊÅØ
        hook.callback = callback
        hook.dependencies = dependencies ? [...dependencies] : null

        // Â∞ÜÂâØ‰ΩúÁî®Âä†ÂÖ•ÈòüÂàóÔºåÂú®Ê∏≤ÊüìÂÆåÊàêÂêéÊâßË°å
        instance.effects.push({
            index,
            callback,
            dependencies,
        })

        hook.hasRun = true
    }
}

// useMemo ÂÆûÁé∞
function useMemo(factory, dependencies) {
    const currentComponent = window.getCurrentComponent()
    if (!currentComponent) {
        throw new Error("useMemo must be called inside a component")
    }

    const instance = currentComponent
    const index = window.getHookIndex()
    window.setHookIndex(index + 1)

    // ÂàùÂßãÂåñÈí©Â≠ê
    if (instance.hooks[index] === undefined) {
        const value = factory()
        instance.hooks[index] = {
            value,
            dependencies: dependencies ? [...dependencies] : null,
        }
        return value
    }

    const hook = instance.hooks[index]
    const prevDependencies = hook.dependencies

    // Ê£ÄÊü•‰æùËµñÈ°πÊòØÂê¶ÊîπÂèò
    const hasChanged =
        !dependencies ||
        !prevDependencies ||
        dependencies.length !== prevDependencies.length ||
        dependencies.some((dep, i) => !Object.is(dep, prevDependencies[i]))

    if (hasChanged) {
        const value = factory()
        hook.value = value
        hook.dependencies = dependencies ? [...dependencies] : null
        return value
    }

    return hook.value
}

// useCallback ÂÆûÁé∞
function useCallback(callback, dependencies) {
    return useMemo(() => callback, dependencies)
}

// useRef ÂÆûÁé∞
function useRef(initialValue) {
    const currentComponent = window.getCurrentComponent()
    if (!currentComponent) {
        throw new Error("useRef must be called inside a component")
    }

    const instance = currentComponent
    const index = window.getHookIndex()
    window.setHookIndex(index + 1)

    // ÂàùÂßãÂåñÈí©Â≠ê
    if (instance.hooks[index] === undefined) {
        instance.hooks[index] = {
            current: initialValue,
        }
    }

    return instance.hooks[index]
}

function createContext(defaultValue) {
    const context = {
        value: defaultValue,
        subscribers: new Set(),
    };

    function Provider({ value, children }) {
        context.value = value;
        context.subscribers.forEach(update => update());
        return children || null;  // üëà ‰øÆÂ§çÁÇπ
    }

    function useContext() {
        const currentComponent = window.getCurrentComponent();
        if (!currentComponent) {
            throw new Error("useContext must be called inside a component");
        }

        // Êú¨Âú∞Áä∂ÊÄÅÔºå‰øùËØÅÁªÑ‰ª∂ËÉΩÈáçÊñ∞Ê∏≤Êüì
        const [_, setState] = window.useState(0);

        // ËÆ¢ÈòÖÂèòÂåñ
        React.useEffect(() => {
            const update = () => setState(s => s + 1); // Âº∫Âà∂Âà∑Êñ∞
            context.subscribers.add(update);
            return () => context.subscribers.delete(update);
        }, []);

        return context.value;
    }

    return { Provider, useContext };
}


// ÂØºÂá∫ React API
const React = {
    createElement,
    useState,
    useEffect,
    useMemo,  //performance improvement
    useCallback,  //performance improvement
    useRef,
    useReducer,
    createContext,

    Component
}

export default React

